.Map {
  #map {
    width: 100%;
    height: 500px;
    background: #000;
    margin-bottom: 60px;
  }

  .btnSet {
    margin-right: 100px;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    justify-content: flex-end;
    button {
      cursor: pointer;
      height: 30px;
      border: 1px solid #888;
      padding: 5px 20px;
    }

    ul {
      display: flex;

      li {
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        border: 1px solid #aaa;
        height: 30px;
        width: 100%;
        max-width: 200px;
        padding: 30px 60px;
        margin-left: 20px;
        transition: 0.5s;
        @include titleStyle2($fz: $fontXXL);
        &.on {
          background: #000;
          border: 1px solid #000;
          color: #fff;
        }
      }
    }
  }
}

@media screen and (max-width: $mp) {
  .Map {
    .btnSet {
      button {
        width: 100%;
        margin-bottom: 10px;
      }

      ul {
        width: 100%;
        justify-content: space-between;

        li {
          margin: 0px;
          width: 33%;
          padding: 0px;
        }
      }
    }
  }
}
